.thank-section {
	position: relative;
	padding: 0 0 10rem;
	text-align: center;
	@include max-md {
		padding: 0 0 8rem;
	}
	.orn-1 {
		position: absolute;
		top: calc(8rem + 20%);
		left: 0;
		width: 260px;
		transform: translate(-60%, 0);
		@include min-md {
			top: calc(8rem + 30%);
			width: 320px;
		}
		@include min-lg {
			width: 340px;
		}
	}
	.orn-2 {
		position: absolute;
		top: calc(3rem + 20%);
		right: 0;
		width: 260px;
		transform: translate(60%, 0) scaleX(-1);
		z-index: 5;
		@include min-md {
			width: 320px;
		}
		@include min-lg {
			width: 340px;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(60%, -40%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 220px;
				transform: translate(50%, -40%);
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-30%, 40%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 120px;
			transform: translate(50%, 25%);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
			.orn-5 {
				position: absolute;
				top: 0;
				right: 0;
				width: 120px;
				transform: translate(60%, -5%) scaleX(-1);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 60%);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 220px;
		}
		.image-element {
			height: auto !important;
		}
	}
	.title-section {
		position: absolute;
		top: 3rem;
		left: 98%;
		transform: scale(-1);
		writing-mode: vertical-rl;
		text-orientation: mixed;
		color: var(--color-dark);
		z-index: 10;
		@include max-md {
			top: 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 65%;
		height: auto;
		aspect-ratio: 16/9;
		z-index: 3;
		@include max-lg {
			aspect-ratio: 6/5;
		}
		@include max-md {
			width: 80%;
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 8rem 2rem 3rem;
			margin-top: -5rem;
			@include max-md {
				padding: 8rem 1rem 2rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				opacity: 0.7;
				content: "";
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
