.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 0;
		width: 260px;
		transform: translate(-60%, 0);
		@include min-md {
			width: 320px;
			transform: translate(-50%, 0);
		}
		@include min-lg {
			width: 340px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-30%, 0);
			@include min-md {
				width: 160px;
				transform: translate(-40%, 0);
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 260px;
		transform: translate(60%, 0) scaleX(-1);
		@include min-md {
			width: 320px;
			transform: translate(50%, 0) scaleX(-1);
		}
		@include min-lg {
			top: 8%;
			width: 340px;
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 160px;
				transform: translate(-40%, 0);
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120px;
		transform: translate(-30%, 40%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 120px;
			transform: translate(50%, 25%);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
			.orn-5 {
				position: absolute;
				top: 0;
				right: 0;
				width: 120px;
				transform: translate(60%, -5%) scaleX(-1);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 6rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
