.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.orn-1 {
		position: absolute;
		top: 1rem;
		left: 0;
		width: 140px;
		transform: translate(-40%, 0);
		z-index: 3;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
			.container-couple {
				padding: 0;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translate(50%, 0) scaleX(-1);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 280px;
			}
			.orn-3 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 120px;
				transform: translate(-30%, 0);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 180px;
					transform: translate(-25%, 20%);
				}
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
		padding: 1.5rem 1.5rem 0;
		@include max-md {
			padding: 1rem 1rem 0;
		}
		&-body {
			position: relative;
			padding: 2rem;
			z-index: 5;
			@include max-md {
				padding: 2rem 1rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				opacity: 0.7;
				content: "";
			}
			p {
				margin-bottom: 0;
			}
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.couple {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		.image-wrap {
			position: relative;
			padding: 1rem;
			width: 100%;
			.image-element {
				position: relative;
				width: 70%;
				height: auto;
				aspect-ratio: 3/4;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: var(--border-radius);
				}
			}
			.elements-wrapper-absolute {
				.background-element {
					img {
						border-radius: var(--border-radius);
					}
				}
			}
		}
		.couple-description {
			position: relative;
			text-align: left;
			padding: 1.5rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
