.countdown {
	display: flex;
	text-align: center;
	justify-content: center;
	position: relative;
	margin: 0 auto;
	width: fit-content;
	gap: 1rem;
	z-index: 3;
	@include max-md {
		gap: 0.5rem;
	}
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		color: var(--color-light);
		.huruf {
			font-size: 14px;
			font-weight: 600;
			text-shadow: 1px 1px 2px #000;
			@include max-xs {
				font-size: 12px;
			}
		}
		.angka {
			position: relative;
			margin-bottom: 0;
			font-weight: 500;
			font-size: 38px;
			font-family: $font-secondary;
			padding: 1.5rem 0.5rem;
			min-width: 80px;
			@include max-md {
				font-size: 28px;
				min-width: 65px;
			}
			@include max-xs {
				min-width: 63px;
				font-size: 24px;
			}
			@include max-rwd(350px) {
				min-width: 72px;
			}
			&::before {
				position: absolute;
				inset: 0;
				background: var(--color-secondary);
				border-radius: 300px;
				opacity: 0.6;
				z-index: -1;
				content: "";
			}
		}
		.huruf,
		.angka {
			margin: 0;
		}
	}
}
