.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 320px;
		transform: translate(-45%, 0);
		@include max-lg {
			top: 35%;
			transform: translate(-50%, 0);
		}
		@include max-md {
			top: 20%;
			width: 280px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 35%;
		right: 0;
		width: 320px;
		transform: translate(45%, 0) scaleX(-1);
		@include max-lg {
			top: 40%;
		}
		@include max-md {
			top: 30%;
			width: 280px;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6.5rem;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				width: 35%;
				@include max-xl {
					width: 40%;
				}
				@include max-lg {
					width: 70%;
				}
				@include max-md {
					width: 90%;
				}
				&::before {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 80%;
					background-color: var(--color-primary);
					opacity: 0.7;
					border-radius: var(--border-radius);
					content: "";
				}
				.greeting-wrapper {
					position: relative;
					margin-bottom: 1rem;
				}
				h6 {
					font-weight: 700;
					margin-bottom: 0;
				}
				.image-wrapper {
					position: relative;
					width: 80%;
					height: auto;
					margin: 0 auto;
					aspect-ratio: 1;
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: var(--border-radius);
					}
				}
				.cover-description {
					padding: 1rem 2rem 0;
					text-align: center;
					@include max-lg {
						padding: 1rem 1rem 0;
					}
					p {
						margin-bottom: 0;
					}
					.title {
						margin-bottom: 0;
						padding: 1rem 0;
					}
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
				}
			}
			.elements-widget {
				width: 100%;
				.btn-custom {
					margin-bottom: -1.5rem;
				}
			}
		}
	}
}
